.bookmark {
  position: absolute;
}
.bookmark .before {
  /**/
  position: absolute;
  /**/
  top: 0;
  left: 0;
  z-index: 1;
  height: 0;
  padding-right: 10px;
  font-weight: bold;
  line-height: 0;
  color: #fff;
  border-right-color: transparent;
  /**/
  box-shadow: 0 5px 5px -5px #000;
}
.bookmark .after {
  /**/
  position: absolute;
  top: 30px;
  left: 0px;
  border: 4px solid rgba(0, 0, 0, 0.7);
  border-left-color: transparent;
  border-bottom-color: transparent;
}
